.footer{
    background-color: #ad740c;
    height:200px;

}
.footerContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items:center;
}

.block {
    width: 280px;
    height: 40px;
    display: flex;
    margin: 0 auto;
    flex-direction: revert;
    justify-content: space-between;
    flex-wrap: wrap;
}
.icon {
    width: 40px;
    height: 40px;
    background-color: ivory;
}

