.contactsBlock {
    height: 100vh;/*видимая высота экрана*/
    background-color: #ccdb87;
}
.contactsContainer {
    flex-direction: column;
    justify-content: flex-start;
}
.textContacts{
    background-color: ivory;
}
.input {
    width: 100%;
    height: 20px;
}
.formContainer {
    width: 280px;
    height: 160px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

}
.textareaContacts{
    width: 100%;
    height: 60px;
}
.block{
    width: 120px;
    height: 30px;
    background-color: #c4e8e8;
}